import Vue from 'vue'
import App from './App.vue'
import router from './router'


Vue.config.productionTip = false
//使用mint-ui引入全部组件
import './static/CSS/global.css'
import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);
import Axios from 'axios';
import  Moment from 'moment';

// Axios.defaults.baseURL=
//配置axios
Vue.prototype.$axios = Axios;

// 注册全局导航栏组件
import Navbar from './components/Common/Navbar';
Vue.component(Navbar.name,Navbar);

//自定义moment全局过滤器
Vue.filter('converTime',function(data,formatStr){
  return Moment(data).format(formatStr);
})

Axios.interceptors.request.use(function (config) {
  // 请求之前，做点事
  // config.headers={"Content-Type":"xxxxx"}  添加请求头
  // console.log('显示loading')
  store.dispatch('showVsByAction',true)
  return config;
}, function (error) {
  // 错误请求，做点事
  return Promise.reject(error);
});

// 响应式，数据返回时
Axios.interceptors.response.use(function (response) {
  // console.log('loading消失')
  store.dispatch('showVsByAction',false)
  return response;
}, function (error) {
  return Promise.reject(error);
});








Vue.prototype.baseUrl = require('../config/config').active


//讲bus这个对象挂载到VUE的原型上，公交车对象
import EventBus from './EventBus'
Vue.prototype.$bus = EventBus

import store from './store'
import "./loading"

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
